<template>
  <v-container fluid fill-height style="padding:0;" id="usp" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="URLSearchParams">URLSearchParams</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1"><code>URLSearchParams</code>对象目的是处理url中的query查询参数，可以将其方便的进行键值对和字符串的双向转化</p>
                    <p class=".body-1">创建<code>URLSearchParams</code>对象的几种方式：</p>
                    <pre class="code">{{pres[0]}}</pre>
                    <p class=".body-1"><code>URLSearchParams</code>实例对象的属性和方法：</p>
                    <pre class="code">{{pres[1]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      pres: 
[
    `
    1. 根据FormData对象创建
        const usp = new URLSearchParams(new FormData(form));

    2. 根据url创建
        const url = new URL('https://www.baidu.com?a=1&b=2')
        const usp = new URLSearchParams(url.search);

    3. 根据数组创建
        const usp = new URLSearchParams([["a", 1],["b", 2]]); 
        usp.toString(); // a=1&b=2

    4. 根据对象创建
        const usp = new URLSearchParams({"a" : 1 , "b" : 2});
    `,
    `
    1. 根据键名获取
        usp.get("a");

    2. 根据键名获取所有
        usp.getAll("a");
    
    3. 添加键值对
        usp.append('c', 3);
    
    4. 根据键名删除
        usp.delete('c');

    5. 判断是否包含
        usp.has("a");

    6. 获取可遍历对象
        usp.entries();
        usp.keys();
        usp.values();

    7. 更新
        usp.set('c', 3);

    8. 根据键名的unicode码点排序
        usp.sort();

    9. 遍历
        usp.forEach((value, key) => {
            console.log(value, key);
        })
    `
],
      time: "2019-07-18",
      title: "URLSearchParams",
      items: [
        {
          title: "URLSearchParams",
          target: "#URLSearchParams"
        },
        { divider: true },
      ],
    };
  },
  mounted() {
  },
  methods: {}
};
</script>

